// frontend/src/app/layout.tsx
import type { Metadata } from "next";
// Corrigido para importar GeistSans e GeistMono corretamente se estiver usando 'next/font/google'
// Se você estiver importando 'Geist' e 'Geist_Mono' de outro lugar ou diretamente, ajuste conforme necessário.
// A implementação original usava 'Geist' e 'Geist_Mono' que parecem ser as exportações corretas da biblioteca.
import { Geist, Geist_Mono } from "next/font/google"; //
import "./globals.css"; // Certifique-se que este é o globals.css atualizado
import NavbarLayout from "@/components/NavBar";
import SideMenu from "@/components/SideMenu";
import { ThemeProvider as NextThemesProvider } from "next-themes"; // Renomeie para evitar conflito
// ...
const geistSans = Geist({
  variable: "--font-geist-sans", // Nome da variável CSS para a fonte sans-serif
  subsets: ["latin"],
  // weight: ['400', '500', '600', '700'] // Especifique os pesos que você vai usar
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono", // Nome da variável CSS para a fonte mono
  subsets: ["latin"],
  // weight: ['400']
});

export const metadata: Metadata = {
  title: "Sistema de Avaliação de Desempenho", // Título mais descritivo
  description: "Plataforma para gestão e acompanhamento de avaliações e desempenho.",
};

export default function RootLayout({
  children,
}: {
  readonly children: React.ReactNode;
}) {
  return (
    <html lang="pt" suppressHydrationWarning> {/* suppressHydrationWarning pode ser útil com temas */}
      <body 
        className={`${geistSans.variable} ${geistMono.variable} font-sans antialiased`}
        // As variáveis --background e --foreground no body serão aplicadas
        // com base no estado do ThemeProvider (light-mode ou dark-mode class)
        // O globals.css já define `background-color: var(--background);` e `color: var(--foreground);` para o body.
      >
        <ThemeProvider
          attribute="class" // Se o ThemeProvider do shadcn/ui ou similar for usado, ele adiciona 'light' ou 'dark' ao <html>
          defaultTheme="light" // Definindo o tema claro como padrão para corresponder à referência
          enableSystem={false} // Desabilitar tema do sistema se quiser forçar light/dark manual
        >
          <div className="flex flex-col min-h-screen">
            {/* NavbarLayout é fixed, então ele se posiciona corretamente */}
            <NavbarLayout><></></NavbarLayout>

            <div className="flex flex-1 pt-16"> {/* pt-16 para compensar a altura da Navbar (h-16) */}
              {/* SideMenu também é fixed */}
              <SideMenu />
              
              {/* Conteúdo Principal */}
              {/* A classe bg-background garantirá o fundo da página (ex: cinza claro) */}
              {/* A classe text-foreground garantirá a cor do texto base */}
              <main className="flex-1 pl-64 bg-background text-foreground overflow-y-auto">
                {/* pl-64 para compensar a largura do SideMenu (w-64 = 16rem) */}
                {/* O padding interno do conteúdo pode ser adicionado aqui ou na classe page-container */}
                <div className="page-container"> {/* Classe do globals.css para padding e max-width */}
                  {children}
                </div>
              </main>
            </div>
          </div>
        </ThemeProvider>
      </body>
    </html>
  );
}